import { FaTimes } from 'react-icons/fa'

function Task({ task, onDoubleClick, remove }) {
    return (
        <div className={`task ${task.reminder ? 'reminder' : ''}`} onDoubleClick={() => onDoubleClick(task.id)}>
            <h5>{task.text}<FaTimes style={{ color: 'red', float: 'right' }} onClick={() => remove(task.id)} /></h5>
            <p>{task.day}</p>
        </div>
    )
}

export default Task
