import { useState } from 'react'
function AddTask({ addTask }) {
    const [day, setDay] = useState('')
    const [text, setText] = useState('')
    const [reminder, setReminder] = useState(false)
    const sub = (e) => {
        e.preventDefault()
        if (!text) {
            alert('Enter Text')
            return
        }
        addTask({ text, day, reminder })
        setText('')
        setDay('')
        setReminder(false)
    }
    return (
        <form className='add-task' onSubmit={sub}>
            <div className='form-group'>
                <label>Text</label>
                <input className='form-control' value={text} type='text' placeholder='Add Text' onChange={(e) => { setText(e.target.value) }} />
            </div>
            <div className='form-group'>
                <label>Date Time</label>
                <input type="text" className='form-control' value={day} placeholder='Add Date' onChange={(e) => { setDay(e.target.value) }} />
            </div>
            <div className='form-group'>
                <input type='checkbox' className='checkbox' checked={reminder} onChange={(e) => setReminder(e.currentTarget.checked)} />
                <label>reminder</label>
            </div>
            <input type='submit' className='btn btn-primary form-control' value='Add Task' />
        </form>
    )
}

export default AddTask
